"use client";

import { useState } from "react";

import Contact from "../Main/contact";
import Navbar from "../Main/navbar";
import Projects from "../Main/projects";
import Resume from "../Main/resume";
import About from "../Main/about";

const Right = () => {
  const [activeComponent, setActiveComponent] = useState("About"); // Default to "About"

  return (
    <div className=" ">
      {/* Navbar - Sticky at the top */}
      <div className="sticky top-0 z-10 bg-white shadow-md  justify-center items-center justify-items-center ">
        <Navbar setActiveComponent={setActiveComponent} />
      </div>

      {/* Conditionally Render Components */}
      <div>
        {activeComponent === "About" && <About />}
        {activeComponent === "Projects" && <Projects />}
        {activeComponent === "Resume" && <Resume />}
        {activeComponent === "Contact" && <Contact />}
      </div>
    </div>
  );
};

export default Right;
"use client";

import { useState } from "react";

const Navbar = ({ setActiveComponent }) => {
  const navItems = ["About", "Projects", "Resume", "Contact"];

  return (
    <div className="">
      <div className="font-bold text-green-400">
        {navItems.map((item) => (
          <span
            key={item}
            className="cursor-pointer mx-2 text-red-500 hover:text-green-600"
            onClick={() => setActiveComponent(item)}
          >
            {item}
          </span>
        ))}
      </div>
    </div>
  );
};

export default Navbar;

